@import url("//maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css");
@import "theme";

//////////////// General App Style ///////////////////

$mainFont: Helvetica, Arial, sans-serif;
$headerFont: Helvetica, Arial, sans-serif;
$primaryFontColor: #101010;
$secondaryFontColor: #505050;
$panelBackground: #fff;
$headerBackground: #fff;
$border: 1px solid  rgba(44, 62, 80, .2);
$border2: 1px solid rgba(152, 168, 183, 0.4);

$whiteBlue: rgba(242, 248, 249, 0.61);
$extraLightBlue: #E1E5E5;
$lightBlue: #ecf0f1;
$lightMiddleBlue: #ecf0f1;
$middleBlue: #ecf0f1;
$darkBlue: rgb(44, 62, 80);
$lightGreen: #cbe347;
$middleGreen: #a5bf16;
$middleGrey: #505050;
$darkGrey: #101010;

$selected: white;

@mixin rounded{
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

@mixin transition {
    -webkit-transition: all 0.25s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    -ms-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

@mixin subtle{
    font-weight:400;
    color: #5e5e5e;
    text-align: justified;
}

#vlab {
     @include transition;

     //background: $extraLightBlue;
     background: #dddddd !important;

     padding: 0px;
     user-select: none;
     -moz-user-select: none;
     -webkit-user-select: none;
     overflow: auto;
 }

a {
    color: #a5bf16;
    background: none !important;
    border: none !important;
    cursor: pointer;
    &:hover{
        color: #a5bf16;
    }
}

html, body {
    min-height: 100%;
}


/////////////// Glyphicon styles ///////////////////

.glyphicon-remove{
    @include transition;

    float: right;
    opacity: .5;

    &:hover {
        opacity: 1;
        cursor: pointer;
    }
}

