////////////////// Popovers ////////////////

.inputWarning{
    display: none;
    font-size: 12px;
    padding: 5px;
    color: rgb(255, 171, 171);
}

.popover{
    border: none;
    max-width: none;
    padding: 0px;
    z-index: 1001;
}

.arrowBox {
    padding: 7px;
    border-radius: 4px;
    position: relative;
    background: #3e444c;
    border: 1px solid rgba(0, 0, 0, 0.6);
}
.arrowBox:after, .arrowBox:before {
    right: 100%;
    top: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.arrowBox:after {
    border-color: rgba(62, 68, 76, 0);
    border-right-color: #3e444c;
    border-width: 10px;
    margin-top: -10px;
}
.arrowBox:before {
    border-color: rgba(0, 0, 0, 0);
    border-right-color: rgba(0, 0, 0, 0.6);
    border-width: 11px;
    margin-top: -11px;
}

#vesselInsulatedInput {
    width: 43px;
    height: 43px;
    position: relative;
    margin: 20px auto;
}

