////////////////// Instrument Panel ///////////////////

.bar.positive {
  fill: steelblue;
}

.bar.negative {
  fill: brown;
}

.axis text {
  font: 10px sans-serif;
}

.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

#instrumentPanelHeader{
    @include rounded;

    //background: $lightMiddleBlue;
    background: #DEDEDE !important;
    color: $darkBlue;
    font: $headerFont;
    border: $border;

    width: 100%;
    position: relative;
    text-align: center;
    z-index: 9;
    padding: 10px;

    //&:hover{
    //    background: $lightBlue;
    //}
}


#instrumentPanelHeader.selected{
    border-bottom: none;
    background: $lightBlue;
}


#instrumentPanelContainer{
    @include transition;
    @include rounded;

    //background: $lightBlue;
    background: #BBBBBB !important;
    border: $border;

    width: 100%;
    //height: 397px;
    opacity: 1;
    top: -5px;
    position: relative;
    overflow-y:scroll;
}


#instrumentPanelContainer.instrumentPanelClosed{
    @include transition;

    height: 0;
    opacity: 0;
}

.instrumentPanelElement {
    background: $whiteBlue;
    margin: 5px;
    padding: 5px;
        @include rounded;


    border: $border2;
}

#absorbanceGraph{
    width: 100%;
}


table, th, td{
    border-color: $secondaryFontColor !important;
    padding: 2px !important;
    margin: 0px !important;
}

.infoCircleTag {
    font-size: 13px;
}

.infoInnerCircle{
    width: 10px;
    height: 10px;
    border-radius: 5px;
    display: inline-block;
    position: relative;
    bottom: 4px;
}

.infoInnerCircle.selected{
    background: $primaryFontColor;
}

.infoOuterCirlce{
width: 14px;
height: 14px;
background: #FBFCFE;
border-radius: 7px;
display: inline-block;
text-align: center;
position: relative;
top: 5px;
margin-left: 5%;
}

.progress {
    height: 30px;
    background: rgb(244, 246, 247);
    border: 1px solid #ccc;
}
.progress-bar {
    padding-top: 6px;
}

.infoChoice, .spectrometerChoice{
    cursor: pointer;
}

//////////////////////////////////////////////////
///                 Stockroom                   //
//////////////////////////////////////////////////

.branch{
    font-weight: normal;
    font-size: 14px;
    cursor: pointer;
    padding-left: 10px;
}

.topBranch{
    font-weight: bold;
    font-size: 16px;
    padding-left: 0px;
}


#stockroomHeader {
    @include rounded;

    //background: $lightMiddleBlue;
    background: #DEDEDE !important;
    color: $darkBlue;
    font: $headerFont;
    border: $border;

    width: 100%;
    position: relative;
    text-align: center;
    z-index: 9;
    padding: 10px;

    //&:hover{
    //    background: $lightBlue;
    //}
}

.stockroomTab {
    border: $border;
    //background: $lightMiddleBlue;
    background: #DEDEDE !important;
    color: $darkBlue;

    text-align: center;
    z-index: 3;
    width:calc(100% / 3);

    &:hover, &:active, &:focus{
        color: $darkGrey;
        //background: $lightBlue;
        background: #DEDEDE !important;
        border: $border;
    }
}

.stockroomTab.selected{
    color: $darkGrey;
    //background: $lightBlue;
    background: #BBBBBB !important;
    border: $border;
    border-bottom: none;
}

#stockroomTabs{
    width: 100%;
}


.branch {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
}



.toolPreview {
    height: 100px;
    position: relative;
}



.stockroomLabel{
    margin-left: 50px;
    font-size: 12px;
    display: inline-block;
}



// The display # of solutions in each category
.solutionCount, .glasswareCount {
    font-weight: normal;
    font-size: 14px;
    opacity: .8;
}

// The box containing solution icon and label
.solutionPreview, .glasswarePreview, .toolPreview{
    @include rounded;

    border: $border2;

    font-weight: normal;
    font-size: 14px;
    padding: 10px;
    margin: 5px !important;
    //margin-left: 20px;
    height: 80px;
    position: relative;
    background: $whiteBlue;

    .stockroomIcon{
        height: 60px;
        position: absolute;
        left: 0px;
        top: 7px;
    }

    .addButton{
        width: 25px;
        height: 25px;
        position: absolute;
        right: 0px;
        top: 0px;
        font-size: 12px;
        cursor: pointer;
        padding: 0px;
        padding-top: 5px;
        border-radius: 0px 3px 0px 3px;

        color: $darkBlue;
        //background: $lightBlue;


        &:hover{
            color: $darkBlue;
        }
    }
}

#stockroomContainer{
    @include transition;
    @include rounded;

    background: $lightMiddleBlue;
    border: $border;

    width: 100%;
    //height: 450px;
    opacity: 1;
    position: relative;
    top: -2px;
    background: #BBBBBB !important;
    overflow-y: scroll;
    text-align: left;
}


#stockroomContainer.stockroomClosed{
    @include transition;

    height: 0;
    opacity: 0;
    padding-top: 0px;
}


